import { useQuery } from "react-query";
import { fetchCoinHistory } from "../api";
import ApexChart from "react-apexcharts";
import { useRecoilValue } from "recoil";
import { isDarkAtom } from "./atoms";

interface IHistorycal {
  time_open: number;
  time_close: number;
  open: string;
  high: string;
  low: string;
  close: string;
  volume: string;
  market_cap: number;
}

interface ChartProps {
  coinId: string;
}

const chartOptions = {
  chart: {
    height: 500,
    width: 500,
    toolbar: {
      show: false,
    },
    background: "transparent",
  },
  stroke: {
    curve: "smooth" as const, // 여기에 'as const'를 추가하여 타입을 명시적으로 지정합니다.
    width: 4,
  },
  grid: { show: false },
  yaxis: { show: false },
  xaxis: {
    axisBorder: { show: false },
    axisTicks: { show: false },
    labels: { show: false },
    type: "datetime",
  },
  fill: {
    type: "gradient",
    gradient: { gradientToColors: ["#0be881"], stops: [0, 100] },
  },
  colors: ["#0fbcf9"],
  tooltip: {
    y: {
      formatter: (value: number) => `$${value.toFixed(2)}`,
    },
  },
};

function Chart({ coinId }: ChartProps) {
  const isDark = useRecoilValue(isDarkAtom);
  const { isLoading, data, isError } = useQuery<IHistorycal[]>(
    ["ohlcv", coinId],
    () => fetchCoinHistory(coinId)
  );

  if (isError) return <div>Error loading data.</div>;
  if (isLoading || !data) return <div>Loading...</div>;

  const seriesData = data.map((price) => ({
    x: new Date(price.time_close * 1000),
    y: [
      Number(price.open),
      Number(price.high),
      Number(price.low),
      Number(price.close),
    ],
  }));

  return (
    <div>
      <ApexChart
        type="candlestick"
        series={[
          {
            name: "Price",
            data: seriesData,
          },
        ]}
        options={{
          ...chartOptions,
          theme: {
            mode: isDark ? ("dark" as const) : ("light" as const), // 여기에 'as const'를 추가하여 타입을 명시적으로 지정합니다.
          },
          xaxis: {
            axisBorder: { show: false },
            axisTicks: { show: false },
            labels: { show: false },
            type: "datetime" as const, // 여기서 "as const"를 추가하여 명시적으로 타입을 지정해줍니다.
          },
        }}
      />
    </div>
  );
}

export default Chart;
