import styled from "styled-components";
import { useQuery } from "react-query";
import { fetchCoinTickers } from "../api";

const Loader = styled.span`
  text-align: center;
  display: block;
`;

const PriceTap = styled.div`
  display: flex;
  flex-direction: column;

  align-items: space;
  width: 100%;
  height: 220px;
  border-radius: 10px;
  background-color: rgba(0, 0, 0, 0.5);
`;

const Main = styled.div`
  display: flex;
  height: 50%;
  width: 100%;
  justify-content: center;
  align-items: center;
  color: ${(props) => props.theme.textColor};
  font-size: 30px;
`;
const Change = styled.div`
  margin-top: 30px;
  color: ${(props) => props.theme.accentColor};
  font-size: 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
`;

const Content = styled.div`
  display: flex;
  width: 100%;
  justify-content: center;
  align-items: center;
  border-radius: 3px;
  border: solid 1px;
  span {
    text-align: center;
  }
`;

interface ChartProps {
  coinId: string;
}

interface Pricedata {
  id: string;
  name: string;
  symbol: string;
  rank: number;
  circulating_supply: number;
  total_supply: number;
  max_supply: number;
  beta_value: number;
  first_data_at: string;
  last_updated: string;
  quotes: {
    USD: {
      ath_date: string;
      ath_price: number;
      market_cap: number;
      market_cap_change_24h: number;
      percent_change_1h: number;
      percent_change_1y: number;
      percent_change_6h: number;
      percent_change_7d: number;
      percent_change_12h: number;
      percent_change_15m: number;
      percent_change_24h: number;
      percent_change_30d: number;
      percent_change_30m: number;
      percent_from_price_ath: number;
      price: number;
      volume_24h: number;
      volume_24h_change_24h: number;
    };
  };
}

function Price({ coinId }: ChartProps) {
  const { isLoading, data } = useQuery<Pricedata>(
    ["tickers", coinId],
    () => fetchCoinTickers(coinId),
    {
      refetchInterval: 5000,
    }
  );
  return (
    <div>
      {isLoading ? (
        <Loader>Loading...</Loader>
      ) : (
        <>
          <PriceTap>
            <Main>
              <span>Price: {data?.quotes.USD.price.toFixed(2)}$</span>
            </Main>
            <Change>
              <Content>
                <span>Change(1H) {data?.quotes.USD.percent_change_1h}%</span>
              </Content>
              <Content>
                <span>Change(12H) {data?.quotes.USD.percent_change_12h}%</span>
              </Content>
              <Content>
                <span>Change(24H) {data?.quotes.USD.percent_change_24h}%</span>
              </Content>
            </Change>
          </PriceTap>
        </>
      )}
    </div>
  );
}

export default Price;
